import React, { memo } from 'react';
const AuthorAvatar = ({ username, network, authorClass, errorPic }) => {
    const background = network.color == '#ffffff' ? '#000' : network.color
    return <img className={`${authorClass}__`} src={errorPic ? errorPic : `https://ui-avatars.com/api/?name=${username.replace(/\s/g, '')}&background=${String(network.id === null ? `#000` : background).replace("#", '')}&color=fff&length=1`} alt={username} width={45} height={45} aria-label="author image" />
}

export default memo(AuthorAvatar);